import {
	mapState,
	mapMutations
} from 'vuex';
import {
	getInfo,
	getDyn,
	checkIsCon,
	concern
} from '../../api/api.js';

export default {
	data() {
		return {
			userInfo: '',
			dynInfo: '',
			statusInfo: '',
			uid: '',
			distance:''
		}
	},
	methods: {
		getUserInfo() {
			getInfo(this.uid).then(data => {
				this.userInfo = data;
				// console.log(data)
			});
			getDyn(this.uid).then(data => {
				this.dynInfo = data.resp;
			});
		},
		getIsCon(){
			checkIsCon(this.uid).then(data=>{
				// console.log(data)
			})
		},
		preview(type, index) {
			let imgUrl = [];
			if (type == 'por') imgUrl = [this.userInfo.portrait];
			if (type == 'dyn') imgUrl = this.dynInfo;
			// uni.previewImage({
			// 	current:index,
			// 	urls: imgUrl,
			// 	longPressActions: {
			// 		itemList: ["保存相冊"],
			// 		success: function(data) {
			// 			console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
			// 		},
			// 		fail: function(err) {
			// 			console.log(err.errMsg);
			// 		}
			// 	}
			// });

			uni.previewImage({
				current: index,
				urls: imgUrl,
			});
		}
	},
	onLoad(option) {
		// console.log(option.uid);
		this.uid = option.uid;
		this.distance =option.distance
		// console.log(option.distance)
	},
	onShow() {
		this.getUserInfo();
		this.getIsCon()
	}
}
